import React from "react";

function InfoCard() {
  return (
    <div className="cards">
      <div className="card">
        <img src={require("./city_images/city-1.jpg").default} alt="" />
        <p className="card-desc">
          Discover <br /> Houston
        </p>
      </div>
      <div className="card">
        <img src={require("./city_images/city-2.jpg").default} alt="" />
        <p className="card-desc">
          Discover <br /> Austin
        </p>
      </div>
      <div className="card">
        <img src={require("./city_images/city-3.jpg").default} alt="" />
        <p className="card-desc">
          Discover <br /> San Francisco
        </p>
      </div>
    </div>
  );
}

export default InfoCard;
